.Modal {
  position: fixed;
  z-index: 500;
  width: 35%;
  left: 35%;
  top: 10%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.Name {
  margin-bottom: 12px;
  background-color: #61c9ff;
  text-align: center;
  color: #ffffff;
}

.Modal2 {
  position: fixed;
  z-index: 500;
  width: 50%;
  left: 25%;
  top: 10%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}


